body {
  text-align: center;
  color: #333;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

h1 {
  color: #fff;
  background-color: #0078d7;
  padding: 15px;
  font-size: 28px;
  font-weight: bold;
}

header {
  color: #fff;
  text-transform: uppercase;
  background-color: #0078d7;
  padding: 10px 0;
  font-size: 22px;
  font-weight: bold;
  box-shadow: 0 4px 8px #0000001a;
}

main {
  background-color: #fff;
  border-radius: 10px;
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 4px 12px #0000001a;
}

input[type="text"] {
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin: 10px 0;
  padding: 12px;
  font-size: 14px;
}

input:focus {
  outline: 3px solid orange;
  transition: outline .2s;
  box-shadow: 0 0 5px #ffa50099;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #0078d7;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  font-size: 14px;
  transition: background-color .3s, transform .2s;
}

button:hover {
  background-color: #005bb5;
  transform: translateY(-2px);
}

button:focus {
  outline: 3px solid orange;
  box-shadow: 0 0 5px #ffa50099;
}

#signIn {
  color: #fff;
  background-color: #34a853;
  border-radius: 5px;
  padding: 12px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

#signIn:hover {
  background-color: #2c8b44;
}

#signOutBttn {
  color: #fff;
  background-color: #d9534f;
  border-radius: 5px;
  padding: 10px 20px;
  transition: background-color .3s;
}

#signOutBttn:hover {
  background-color: #c9302c;
}

ul {
  margin: 20px 0;
  padding: 0;
  list-style: none;
}

li {
  text-align: left;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin: 10px 0;
  padding: 12px;
  transition: background-color .2s;
  box-shadow: 0 2px 4px #0000000d;
}

li:hover {
  background-color: #f0f8ff;
}

.delete-btn {
  color: #fff;
  cursor: pointer;
  float: right;
  background-color: red;
  border-radius: 4px;
  padding: 5px 10px;
  transition: background-color .3s;
}

.delete-btn:hover {
  background-color: #8b0000;
}

#chatbot-container {
  z-index: 1000;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 320px;
  padding: 12px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

#chat-history {
  color: #333;
  scrollbar-width: thin;
  scrollbar-color: #ccc #f9f9f9;
  background-color: #f9f9f9;
  border-bottom: 1px solid #ccc;
  border-radius: 5px;
  height: 200px;
  margin-bottom: 10px;
  padding: 8px;
  font-size: 14px;
  overflow-y: auto;
}

.history {
  color: #333;
  background-color: #e7e7e7;
  border-radius: 5px;
  margin-bottom: 8px;
  padding: 8px;
  font-size: 13px;
}

.history.user {
  text-align: left;
  background-color: #e0f7fa;
  font-weight: bold;
}

.history.ai {
  text-align: left;
  background-color: #e8f5e9;
  font-weight: bold;
}

#chat-input {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: calc(100% - 80px);
  padding: 8px;
  font-size: 14px;
}

#send-btn {
  color: #fff;
  background-color: #0078d7;
  border-radius: 5px;
  padding: 8px 15px;
  font-size: 14px;
  transition: background-color .3s;
}

#send-btn:hover {
  background-color: #005bb5;
}

@media (width <= 768px) {
  main {
    width: 100%;
    padding: 15px;
  }

  #chatbot-container {
    width: 90%;
    bottom: 10px;
    right: 10px;
  }

  input[type="text"] {
    font-size: 14px;
  }

  button {
    padding: 8px;
    font-size: 14px;
  }
}

@media (width <= 480px) {
  h1 {
    font-size: 22px;
  }

  #chatbot-container {
    width: 95%;
    bottom: 5px;
    right: 5px;
  }

  button {
    padding: 6px 10px;
    font-size: 12px;
  }

  input[type="text"] {
    padding: 8px;
    font-size: 12px;
  }
}
